<template> <!-- "Y-m-d H:i:s" -->
<v-container grid-list-ml>
    <v-layout v-bind="binding">
        <v-flex>
            <div class="block"><p class="digit display-3">{{ days | two_digits }}</p>
                <p class="text display-2">Jours</p></div>
        </v-flex>
        <v-flex>
            <div class="block"><p class="digit display-3">{{ hours | two_digits }}</p>
                <p class="text display-2">Heures</p></div>
        </v-flex>
        <v-flex>
            <div class="block"><p class="digit display-3">{{ minutes | two_digits }}</p>
                <p class="text display-2">Minutes</p></div>
        </v-flex>
        <v-flex>
            <div class="block"><p class="digit display-3">{{ seconds | two_digits }}</p>
                <p class="text display-2">Secondes</p></div>
        </v-flex>
    </v-layout>
</v-container> </template>
<script> export default {
    name: "countdown", template: "#countdown-template", mounted() {
        window.setInterval(() => {
            this.now = Math.floor(new Date().getTime() / 1000);
        }, 1000);
    }, props: {date: {type: String}}, data() {
        return {now: Math.floor(new Date().getTime() / 1000)};
    }, computed: {
        dateInMilliseconds() {
            return Math.floor(Date.parse(this.date) / 1000);
        }, seconds() {
            return (this.dateInMilliseconds - this.now) % 60;
        }, minutes() {
            return Math.floor((this.dateInMilliseconds - this.now) / 60) % 60;
        }, hours() {
            return Math.floor((this.dateInMilliseconds - this.now) / 60 / 60) % 24;
        }, days() {
            return Math.floor((this.dateInMilliseconds - this.now) / 60 / 60 / 24);
        }, binding() {
            const binding = {}
            if (this.$vuetify.breakpoint.smAndDown) binding.column = true
            return binding
        }
    }, filters: {
        two_digits: function (value) {
            if (value < 0) {
                return "00";
            }
            if (value.toString().length <= 1) {
                return `0${value}`;
            }
            return value;
        }
    }
}; </script>
