<template>
            <v-layout flat>
                <v-flex xs12 md10 offset-md1>
                    <v-card flat>
                        <v-container grid-list-lg fluid>
                            <v-layout row wrap>
                                <v-flex
                                        v-for="(img, i) in images"
                                        :key="i"
                                        xs12
                                        md3
                                        d-flex
                                >
                                    <v-card flat tile class="d-flex">
                                        <v-img
                                                :src="img.thumb_src"
                                                aspect-ratio="1"
                                                v-on:click="openDetail(i)"
                                                class="grey lighten-2 image"
                                        >
                                            <template v-slot:placeholder>
                                                <v-layout
                                                        fill-height
                                                        align-center
                                                        justify-center
                                                        ma-0
                                                >
                                                    <v-progress-circular indeterminate
                                                                         color="grey lighten-5"></v-progress-circular>
                                                </v-layout>
                                            </template>
                                        </v-img>
                                    </v-card>
                                </v-flex>
                            </v-layout>
                        </v-container>
                    </v-card>
                </v-flex>
                <div v-if="selectedIndex !== null" id="overlay" v-on:click="closeDetail()">
                    <v-img class="detailImage" v-on:click.stop="" :src="images[selectedIndex].src" :contain="true"></v-img>
                    <v-btn flat icon dark id="ctrl-left" class="ctrl-nav" >
                        <v-icon v-if="selectedIndex !== 0" x-large
                                @click.stop="previous($event)">chevron_left
                        </v-icon>
                    </v-btn>
                    <v-btn flat icon dark id="ctrl-right" class="ctrl-nav" >
                        <v-icon v-if="selectedIndex !== images.length - 1" x-large
                                @click.stop="next($event)">chevron_right
                        </v-icon>
                    </v-btn>
                    <v-btn flat icon dark id="ctrl-close" class="ctrl-nav" >
                        <v-icon x-large v-on:click="closeDetail()">close</v-icon>
                    </v-btn>
                </div>
            </v-layout>

</template>

<script>
    export default {
        name: "gallery",
        props: ['images'],
        data: function () {
            return {
                selectedIndex: null,
                hoveredIndex: null,
                selectedImg: null
            }
        },
        methods: {
            openDetail: function (i) {
                this.selectedIndex = i;
            },
            closeDetail: function () {
                this.selectedIndex = null;
            },
            next: function () {
                this.selectedIndex = Math.min(this.selectedIndex + 1, this.images.length - 1);
            },
            previous: function () {
                this.selectedIndex = Math.max(this.selectedIndex - 1, 0);
            },
            arrowKeyListener: function(evt) {
                if (evt.key === 'ArrowLeft' && this.selectedIndex !== null) {
                    this.previous();
                } else if (evt.key === 'ArrowRight' && this.selectedIndex !== null) {
                    this.next();
                }
            }
        },
        created: function() {
            document.addEventListener('keyup', this.arrowKeyListener);
        },
        destroyed: function() {
            document.removeEventListener('keyup', this.arrowKeyListener);
        },
    }
</script>

